import { isValidCNPJ, isValidCPF, isValidPhone } from "@brazilian-utils/brazilian-utils";
import isValidCreditCard from "card-validator";
import * as yup from "yup";

export const schema = yup
  .object({
    fullName: yup
      .string()
      .required("Nome e sobrenome, obrigatrio.")
      .min(3, "Nome e sobrenome muito curto!")
      .matches(/(\w.+\s).+/gi, "O nome deve conter o sobrenome."),
    email: yup
      .string()
      .email("Por favor, insira um e-mail vlido.")
      .required("O e-mail  obrigatrio!"),
    mobile: yup
      .string()
      .required("O nmero de telefone  obrigatrio.")
      .matches(/^[0-9]+$/, "O nmero de telefone deve conter apenas dgitos")
      .transform((value) => value.replace(/[^\d]/g, ""))
      .test("validationMobile", "Celular invlido.", (value) => isValidPhone(value)),
    document: yup
      .string()
      .required("O CPF/CNPJ  obrigatrio.")
      .transform((value) => value.replace(/[^\d]/g, ""))
      .test(
        "validateDocument",
        "O CPF/CNPJ  invlido",
        (value) => isValidCPF(value) || isValidCNPJ(value),
      ),
    zipCode: yup
      .string()
      .required("O CEP  obrigatrio.")
      .transform((value) => value.replace(/[^\d]+/g, "")),
    street: yup.string().required("O Endereo  obrigatrio."),
    number: yup.string().required("O Nmero  obrigatrio."),
    complement: yup.string(),
    neighborhood: yup.string().required("O Bairro  obrigatrio."),
    city: yup.string().required("A Cidade  obrigatria."),
    state: yup.string().required("O Estado  obrigatrio."),
    creditCardNumber: yup
      .string()
      .required("O nmero do carto  obrigatrio.")
      .transform((value) => value.replace(/[^\d]/g, ""))
      .test(
        "validateCreditCardNumber",
        "O nmero do carto  invlido.",
        (value) => isValidCreditCard.number(value).isValid,
      ),
    creditCardHolder: yup
      .string()
      .required("O nome do titular  obirgatrio.")
      .min(3, "O nome do titular deve ser completo.")
      .matches(/(\w.+\s).+/gi, "O nome do titular deve conter o sobrenome."),
    creditCardExpiration: yup
      .string()
      .required("A data de validade  obrigatria.")
      .transform((value) => {
        const [month, year] = value.split("/");
        if (month && year && month.length === 2 && year.length === 2) {
          return new Date(+`20${year}`, +month - 1, 1).toISOString();
        }
        return value;
      })
      .test(
        "validateCreditCardExpiration",
        "A data de validade  invlida.",
        (value) => new Date(value) >= new Date(),
      ),
    creditCardSecurityCode: yup
      .string()
      .required("O CVV  obrigatrio.")
      .transform((value) => value.replace(/[^\d]+/g, ""))
      .min(3, "O CVV deve possuir entre 3 e 4 dgitos.")
      .max(4, "O CVV deve possuir entre 3 e 4 dgitos."),
  })
  .required();

export type FieldValues = yup.InferType<typeof schema>;
